<template>
  <div class="pa-4">
    <h1>Aggrid表格图表 默认柱状图+折线图</h1>
    <p class="d-flex flex-wrap">
      <a
        class="mr-2"
        href="https://www.ag-grid.com/vue-data-grid/grid-api/#reference-charts-createRangeChart"
      >Aggrid 文档 | createRangeChart</a>
      <a
        class="mr-2"
        href="https://www.ag-grid.com/vue-data-grid/integrated-charts-api-range-chart/"
      >Aggrid 文档 | Range Chart API</a>
    </p>
    <div id="myChart">
      <ag-grid-vue
        style="height: 300px;"
        class="ag-theme-alpine"
        :column-defs="columnDefs"
        :row-data="rowData"
        :chart-theme-overrides="chartThemeOverrides"
        @first-data-rendered="onFirstDataRendered"
      />
    </div>
  </div>
</template>
<script>
import { LicenseManager } from 'ag-grid-enterprise';
import { AgGridVue } from "ag-grid-vue";

LicenseManager.setLicenseKey('CompanyName=Suzhou ruhlamat Smart Technologies Co., Ltd.,LicensedApplication=Infinity,LicenseType=SingleApplication,LicensedConcurrentDeveloperCount=1,LicensedProductionInstancesCount=30,AssetReference=AG-030422,SupportServicesEnd=30_August_2023_[v2]_MTY5MzM1MDAwMDAwMA==28423dd515d527503fc5a257dc86a8ac');

export default {
  name: "Aggrid",
  components: {
    AgGridVue,
  },
  data() {
    return {
      columnDefs: null,
      rowData: null,
      chartThemeOverrides: null,
    };
  },

  beforeMount() {
    this.columnDefs = [
      { field: "make", chartDataType: 'category' },
      { field: "model" },
      { field: "price", chartDataType: 'series' },
      { field: "percentage", chartDataType: 'series' },
      { field: "number", chartDataType: 'series' },
      { field: "rate", chartDataType: 'series' },
      { field: "rate2", chartDataType: 'series' },
    ];

    this.rowData = [
      { make: "Toyota", model: "Celica", price: 35000, percentage: 25, number: 19999, rate: 18, rate2: 23.1 },
      { make: "Ford", model: "Mondeo", price: 32000, percentage: 50, number: 23456, rate: 88, rate2: 23.1 },
      { make: "Porsche", model: "Boxster", price: 72000, percentage: 62, number: 2333, rate: 108, rate2: 23.1 },
    ];

    this.chartThemeOverrides = {
      common: {
        background: {
          visible: false,
        },
        title: {
          enabled: true,
          text: '测试图表',
        },
        legend: {
          enabled: true,
          position: 'top', // 图例的位置
          item: {
            marker: {
              strokeWidth: 3, // 外边框宽度
            },
          },
        }, 
        navigator: {
          enabled: true,
          height: 15,
        },
        // yAxis: {
        //   title: {
        //     text: 'Shared Y Axis Title',
        //   },
        // },
      },
      bar: {
        series: {
          strokeWidth: 2,
          fillOpacity: 0.8,
        },
      },
      line: {
        series: {
          strokeWidth: 5,
          strokeOpacity: 0.3,
        },
      },
    };
  },

  methods: {
    onFirstDataRendered(params) {
      params.api.createRangeChart({
        chartType: 'customCombo', // 图表类型
        cellRange: {
          columns: ['make', 'price', 'percentage', 'number', 'rate', 'rate2'], // 数据列
        },
        xKey: 'make', // x轴
        seriesChartTypes: [ // 图表类型
          // 'area': 面积图 'bar': 柱状图 'column': 柱状图 'line': 折线图 'scatter': 散点图 'stackedBar': 堆叠柱状图 'stackedColumn': 堆叠柱状图 'bubble': 气泡图
          // groupedColumn 使用同一个y轴标尺
          { colId: 'number', chartType: 'groupedColumn', secondaryAxis: false, },
          { colId: 'price', chartType: 'groupedColumn', secondaryAxis: false },
          { colId: 'percentage', chartType: 'line', secondaryAxis: true, yAxis: { title: '挖槽' }, },
          // ! AG Grid: invalid chartType 'groupedLine' supplied in 'seriesChartTypes', converting to 'line' instead.
          { colId: 'rate', chartType: 'line', secondaryAxis: false,  },
          { colId: 'rate2', chartType: 'line', secondaryAxis: true },
        ],
        aggFunc: 'sum', // 数据聚合
        suppressChartRanges: true, // 显示图表范围
        chartContainer: document.querySelector('#myChart'), // 图表容器
        // chartThemeOverrides: {
        //   common: {
        //     yAxis: {
        //       title: {
        //         text: 'Shared Y Axis Title',
        //       },
        //     },
        //   }
        // }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@import "~ag-grid-community/styles/ag-grid.css";
@import "~ag-grid-community/styles/ag-theme-alpine.css";

</style>